/*header*/

.row .header {
    width: 100%;
    position: relative;
}

* {
    box-sizing: border-box;
}

/* Create three equal columns that floats next to each other */
.column {
    float: left;
    width: 33.33%;
    padding: 10px;
     /* Should be removed. Only for demonstration */
}

/* Clear floats after the columns */
.row:after {
    content: "";
    display: table;
    clear: both;
}
.ui.card, .ui.cards>.card {
    width:100% !important;
}
.showscontainer {
  background-color: #eee;
  letter-spacing: 0px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  text-align: center;
}
.container-all {
  visibility: hidden;
  opacity: 0;
  height: 100%;
  width: 100%;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  transition: all 600ms;
}
.container-all:target{
  background: rgba(0, 0, 0, 0.7);
  visibility: visible;
  opacity: 1;
}

.container-all:target .popup{
  top: 50%;
  left: 50%;
  -webkit-transform: rotate(0deg) translate(-50%, -50%);
          transform: rotate(0deg) translate(-50%, -50%);
  visibility: visible;
}

.popup {
  width: 100%;
  max-width: 900px;
  height: 500px;
  position: relative;
  display: -webkit-flex;
  display: flex;
  background-color: white;
  top: -200%;
  left: -200%;
  -webkit-transform: rotate(90deg) translate(150%, 230%);
          transform: rotate(90deg) translate(150%, 230%);
  transition: all 600ms;
}
.cardvisible {
  width: 250px;
  margin: 0px;
  margin: 20px;
  background-color: white;
  border-radius: 9px;
  box-shadow: 4px 2px 8px rgba(0,0,0,.4);
  overflow: hidden;
  transition: 250ms;
}

.cardvisible:hover {
  -webkit-transform: translate(0px, -3px);
          transform: translate(0px, -3px);
  transition: 250ms;
  box-shadow: 7px 4px 10px rgba(0,0,0,.4);
}

.cardvisible img{
  width: 100%;
}

