.upload-img {
    width: 0.1px;
	height: 0.1px;
	opacity: 0;
	overflow: hidden;
	position: absolute;
	z-index: -1;
}

.upload-img + label {
    height: 20px;
    width: 100%;
    max-width: 250px;
    padding: 12px 20px;
    font-size: 1.25em;
    font-weight: 700;
    background-color:  #659eb3;
    color: white;
    display: inline-block;
    cursor: pointer;
    border-radius: 4px;
}

.upload-img:focus + label,
.upload-img + label:hover {
    background-color: #0296c7;
    color: white;
}

.upload-img:focus + label {
	background-color: #045a77;
} 

